.about-section {
  position: relative;
  padding-top: 12px;
}

.about-edit-btn-container {
  position: absolute;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--dark-white);
  left: -6%;
  top: -5%;
  opacity: 0;
  transition: opacity 200ms ease;
  pointer-events: none;
}

.about-edit-btn-container:hover {
  background-color: var(--bright-white);
}

.visible {
  opacity: 1;
  pointer-events: auto;
  z-index: 1;
}

.section-heading {
  color: var(--left-side-heading-color);
  margin-left: 7px;
}

.about-text {
  color: var(--dark-white);
  overflow-wrap: break-word;
  font-size: 15px;
  padding: 7px;
  border: none;
  font-weight: 500;
}

.about-form {
  position: absolute;
  right: 108%;
  top: 10%;
  background-color: var(--right-side-background);
  width: 70%;
  display: flex;
  flex-direction: column;
  gap: 5px;
  opacity: 0;
  transition: opacity 200ms ease;
  pointer-events: none;
  padding: 10px;
}

textarea {
  border-radius: 2px;
  background-color: var(--bright-white);
  min-height: 70px;
  max-height: 70px;
  min-width: 170px;
  max-width: 170px;
  padding: 0 5px 0 5px;
  border: none;
  margin-top: 5px;
  box-shadow: 1px 1px 1px black;
}

textarea:focus {
  outline: none;
}

@media only screen and (max-width: 600px) {
  .about-section {
    padding: 0;
  }

  .about-edit-btn-container {
    max-height: 30px;
    width: 30px;
  }

  .section-heading {
    margin: 0;
  }

  .about-text {
    font-size: 12px;
    padding: 0;
    font-weight: 400;
  }

  .about-form {
    left: 0;
    top: 2px;
    width: 150%;
    padding: 0px;
  }
  
  textarea {
    min-height: 30px;
    min-width: 100px;
  }
}